import React, {useContext, useState, useRef, useEffect} from "react";
import { useParams } from "react-router-dom";
import VersesContext from "../contexts/BibleContext";
import { CloseIcon } from "./AppIcons";

function VerseInChapter() {
    const { biblija, books } = useContext(VersesContext);
    const { book, chapter, verse_no } = useParams();
    const [theChapter, setTheChapter] = useState([]);

    const hasChapter = useRef(false);


    useEffect(() => {
        if (!hasChapter.current) {
                setTheChapter(
                    biblija.filter(item => (item.book === book & Number(item.chapter) === Number(chapter)))
                );
                hasChapter.current = true;
            }
        }, [biblija, book, chapter])

    const verseHighlighted = {
        background: "yellow",
        opacity: 0.8
    };

    return (
        <div>
            <div className="close-page"><CloseIcon /></div>
            
            <div className="chapter">
            <h3>{books.filter(item => (item.eng_short === book))[0].name} {chapter}:{verse_no}</h3>
            {
                theChapter.map(item => (
                        item.verse_no === Number(verse_no) ? 
                        <span style={verseHighlighted} key={item._id}>
                            <sup>{item.verse_no}</sup>
                                {item.verse_text}
                        </span> : 
                        <span key={item._id}>
                            <sup>{item.verse_no}</sup>
                                {item.verse_text}
                        </span>  
                    )
                )
            }
            </div>
        </div>
    )
}

export default VerseInChapter;