export const collapseMenu = () => {
        document.getElementById("menu-overlay-toggle").checked = false;
    }

function OverlayMenu() {
    return (
        <div id="menu-overlay">
          <input type="checkbox" id="menu-overlay-toggle" name="menu-overlay-toggle" aria-labelledby="menu-overlay-toggle" />
            <label htmlFor="menu-overlay-toggle"></label>
          <ul>
            <li>
              <div onClick={collapseMenu}>
                <HomeIcon label={"Naslovnica"} />
              </div>
            </li><li>
              <div onClick={collapseMenu}>
                <TocIcon label={"Sadržaj"} />
              </div>
            </li><li>
              <div onClick={collapseMenu}>
                <SearchIcon label={"Traženje pojma"} />
              </div>
            </li><li>
              <div onClick={collapseMenu}>
                <SettingsIcon label={"Podešavanje"} />
              </div>
            </li><li>
              <div onClick={collapseMenu}>
                <InfoIcon label={"O aplikaciji"} />
              </div>
            </li>
          </ul>
        </div>
    )
}

export default OverlayMenu;