import "./appicons.css";
import React from "react";
import { AiFillHome } from "react-icons/ai";
import { AiOutlineUnorderedList } from "react-icons/ai";
import { AiOutlineLeft } from "react-icons/ai";
import { AiOutlineRight } from "react-icons/ai";
import { AiOutlineInfoCircle } from "react-icons/ai";
import { AiOutlineSearch } from "react-icons/ai";
import { AiOutlineSetting } from "react-icons/ai";
import { AiOutlineClose } from "react-icons/ai";
import { Link } from "react-router-dom";
import { collapseMenu } from "./OverlayMenu";

export const HomeIcon = (props) => {
    return (
            <Link className="icon-link" to="/">
                <AiFillHome className="icon" />
                <small className="icon-label">
                       {props.label}
                </small>
            </Link>
    )
}

export const TocIcon = (props) => {
    return (
            <Link className="icon-link" to="/toc">
                <AiOutlineUnorderedList className="icon" />
                <small className="icon-label">
                       {props.label}
                </small>
            </Link>
    )
}

export const InfoIcon = (props) => {
    return (
        <Link className="icon-link" to="/about">
           <AiOutlineInfoCircle className="icon" />
            <small className="icon-label">
                {props.label}
            </small>
        </Link>
    )
}

export const SearchIcon = (props) => {
    return (
            <Link className="icon-link" to="/search">
                <AiOutlineSearch className="icon" />
                <small className="icon-label">{props.label}</small>
            </Link>
    )
}

export const SettingsIcon = (props) => {
    return (
            <Link className="icon-link" to="/settings">
                <AiOutlineSetting className="icon" />
                <small className="icon-label">
                    {props.label}
                </small>
            </Link>
    )
}

export const ArrowLeftIcon = (props) => {
    const isDisabled = props.disabled;
    let iconColor = "";
    if (isDisabled) {
        iconColor = "#bdbbbb";
    }
    return <AiOutlineLeft className="arrow-icon"
        style={{ color: iconColor }}
    />
}

export const ArrowRightIcon = (props) => {
    const isDisabled = props.disabled;
    let iconColor = "";
    if (isDisabled) {
        iconColor = "#bdbbbb";
    }
    return <AiOutlineRight className="arrow-icon"
        style={{ color: iconColor }}
    />
}

export const CloseIcon = () => {
    return (
        <div onClick={ () => {
            collapseMenu();
            window.history.back() }
        }>
            <AiOutlineClose className="close-icon" />
        </div>
    )
}
